.blogdiv{

  width: 50%;
  padding: auto;
  margin: 30px;
}

.totaldiv{
  display: flex;
  flex-direction: column;
  align-items: center;
}

.inputBox{
  display: block;
  margin: 20px;
  padding: 7px;
  width: 350px;
  border: 1px solid black;
}

.appbutton{
  margin: 15px;
  padding: 10px;
  width: 150px;
  background-color: skyblue;
  border: 1px solid;
  cursor: pointer;
}

.add{
  display: flex;
  flex-direction: column;
  margin: auto;
  width: fit-content;
  margin-top: 20px;
  align-items: center;
  background-color: #F0F8FF;
  border-radius: 20px;
  margin-bottom: 30px;
  margin-top: 30px;
}

.heading1{
  margin-top: 20px;
}

span{
  color: red;
  margin-top: -20px;
  display: block;
}

.productlist{
  text-align: center;
  margin-top: 50px;
}

.search{
  width: 400px;
  height: 30px;
  margin: 20px;
  padding-left: 10px;
  border: 1px solid skyblue;
}

.button1{
  margin: 10px;
  background-color: skyblue;
  border-radius: 10px;
}

.decor{
  text-decoration: none;
  color: black;
}


.footer{
  position: absolute;
  bottom: 0;
  width: 100%;
  background-color: black;
  color: white;
  padding: 0;
  text-align: center;
}